import AlertCircle from 'vue-material-design-icons/AlertCircle.vue';
import FolderMultipleImage from 'vue-material-design-icons/FolderMultipleImage.vue';
import { NcEmptyContent, isMobile } from '@nextcloud/vue';
import { translate } from '@nextcloud/l10n';
import FilesSelectionMixin from '../../mixins/FilesSelectionMixin.js';
import FilesListViewer from '.././FilesListViewer.vue';
import File from '.././File.vue';
export default {
  name: 'CollectionContent',
  components: {
    AlertCircle,
    FolderMultipleImage,
    NcEmptyContent,
    FilesListViewer,
    File
  },
  mixins: [FilesSelectionMixin, isMobile],
  props: {
    /** @type {import('vue').PropType<import('../../services/collectionFetcher').Collection>} */
    collection: {
      type: Object,
      default: () => undefined
    },
    /** @type {import('vue').PropType<string[]>} */
    collectionFileIds: {
      type: Array,
      required: true
    },
    loading: {
      type: Boolean,
      default: false
    },
    allowSelection: {
      type: Boolean,
      default: true
    },
    error: {
      type: [Error, Number],
      default: null
    }
  },
  data() {
    return {
      appContent: document.getElementById('app-content-vue')
    };
  },
  computed: {
    /** @return {import('../../services/collectionFetcher').IndexedCollectionFiles} */
    files() {
      return this.$store.getters.files;
    }
  },
  methods: {
    openViewer(fileId) {
      const file = this.files[fileId];
      OCA.Viewer.open({
        fileInfo: file,
        list: this.collectionFileIds.map(fileId => this.files[fileId]).filter(file => !file.sectionHeader),
        loadMore: file.loadMore ? async () => await file.loadMore(true) : () => [],
        canLoop: file.canLoop
      });
    },
    t: translate
  }
};