import { mapGetters } from 'vuex';
import { NcButton, NcLoadingIcon } from '@nextcloud/vue';
import { UploadPicker } from '@nextcloud/upload';
import moment from '@nextcloud/moment';
import ImagePlus from 'vue-material-design-icons/ImagePlus.vue';
import FilesListViewer from './FilesListViewer.vue';
import File from './File.vue';
import FetchFilesMixin from '../mixins/FetchFilesMixin.js';
import FilesSelectionMixin from '../mixins/FilesSelectionMixin.js';
import FilesByMonthMixin from '../mixins/FilesByMonthMixin.js';
import UserConfig from '../mixins/UserConfig.js';
import allowedMimes from '../services/AllowedMimes.js';
export default {
  name: 'FilesPicker',
  components: {
    File,
    FilesListViewer,
    ImagePlus,
    NcButton,
    NcLoadingIcon,
    UploadPicker
  },
  filters: {
    /**
     * @param {string} date - In the following format: YYYYMM
     */
    dateMonthAndYear(date) {
      return moment(date, 'YYYYMM').format('MMMM YYYY');
    }
  },
  mixins: [FetchFilesMixin, FilesByMonthMixin, FilesSelectionMixin, UserConfig],
  props: {
    // Label to show in the submit button.
    destination: {
      type: String,
      required: true
    },
    // List of file ids to not show.
    blacklistIds: {
      type: Array,
      default: () => []
    },
    // Whether we should disable the submit button and show a spinner.
    loading: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      allowedMimes,
      targetMonth: null,
      uploadContext: {
        route: 'albumpicker'
      }
    };
  },
  computed: {
    ...mapGetters(['files'])
  },
  watch: {
    monthsList(value) {
      if (this.targetMonth === null) {
        this.targetMonth = value[0];
      }
    }
  },
  methods: {
    getFiles() {
      this.fetchFiles('', {}, this.blacklistIds);
    },
    refreshFiles() {
      this.fetchFiles('', {
        firstResult: 0
      }, [...this.blacklistIds, ...this.fetchedFileIds], true);
    },
    emitPickedEvent() {
      this.$emit('files-picked', this.selectedFileIds);
    }
  }
};